@import url("https://fonts.googleapis.com/css?family=Patrick+Hand|Londrina+Solid:300");

.App {
  text-align: center;
  height: 85vh
}

body {
  background: #EEE;
  font: normal 22px/25px "Londrina Solid", sans-serif;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

h1 {
  margin-top: 0px;
  padding-bottom: 0.75em;
}

#gridCanvas {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
}

canvas {
  align-self: center;
  border-radius: 5px;
  border: 2px solid #000;
}

#buttonContainer {
  align-self: center;
}

button span {
  background: #FFF;
  display: block;
  padding: 5px 15px;
  border-radius: 5px;
  border: 2px solid #000;
}

button:hover {
  box-shadow: 0 2px 0 #000, 0 2px 0px 2px #f03755;
}

button:active {
  top: 4px;
  padding-bottom: 0px;
  box-shadow: 0 1px 0 #000;
}

button:disabled {
  color: #aaa;
  pointer-events: none;
}

button {
  position: relative;
  float: left;
  font: normal 22px/25px "Patrick Hand", sans-serif;
  margin-right: 10px;
  text-transform: uppercase;
  color: #000;
  text-decoration: none;
  padding-left: 0px;
  padding-right: 0px;
  padding-top: 0px;
  padding-bottom: 2px;
  border-radius: 5px;
  border-style: none;
  box-shadow: 0 2px 0 #000;
  transition: padding 0.1s, box-shadow 0.1s, top 0.1s;
  background-image: url('data:image/gif;base64,');
}
